import Nav from "../nav";
import { useEffect, useState } from "react";
import {
  createServiceCategory,
  getAllServiceCategories,
  deleteServiceCategory,
} from "../../../api/server";
import { data } from "jquery";
import { Link } from "react-router-dom";
const AddServiceCategory = (props) => {
  const [tobeEdited, setTobeEdited] = useState(null); //exptects object

  const [title, setTitle] = useState(tobeEdited ? tobeEdited.title : "");
  const [description, setDescription] = useState(
    tobeEdited ? tobeEdited.description : ""
  );
  const [service_id, setServiceID] = useState(
    tobeEdited ? tobeEdited.service_id : 0
  );
  const [loading, setLoading] = useState(false);
  const [loadingPost, setLoadingPost] = useState(false);
  const [loadingCategories, setLoadingCategories] = useState(false);
  const [categories, setCategories] = useState([]);
  const [postingError, setPostingError] = useState(null);
  const [deleteWait, setDeletingWait] = useState(false);
  const postOnServer = async () => {
    setLoadingPost(true);
    try {
      const response = await createServiceCategory({
        title,
        description,
        service_id,
      });
      if (response && response.data.success) {
        // console.log(response.data);
      }
    } catch (e) {
      console.log(e); //to be removed
    }

    setLoadingPost(false);
  };
  useEffect(() => {
    if (!loadingPost) {
      pullCategories();
    }
  }, [loadingPost, service_id, deleteWait]);
  const pullCategories = async () => {
    setLoadingCategories(true);
    try {
      const response = await getAllServiceCategories(service_id);
      if (response.data) {
        setCategories(response.data.contents);
      } else {
        setCategories([]);
      }
    } catch (e) {}
    setLoadingCategories(false);
  };
  const deleteCategory = async (id) => {
    setDeletingWait(true);
    try {
      await deleteServiceCategory(id);
    } catch (e) {
      //   e
    }
    setDeletingWait(false);
  };
  return (
    <div className="" style={{ marginTop: "5rem" }}>
      <Nav />
      <div className="container">
        <div className="row justify-content-center">
          <div className="col-sm-10">
            <h4 className="light-brand-color">
              {tobeEdited ? "Editing" : "Add"} service category
            </h4>
          </div>
        </div>
        <div className="row justify-content-around">
          <div className="col-sm-5">
            <form className="position-relative">
              {tobeEdited ? (
                <span
                  className="position-absolute bg-warning font-weight-bold text-light"
                  style={{
                    width: "40px",
                    height: "40px",
                    borderRadius: "50%",
                    lineHeight: "40px",
                    textAlign: "center",
                    top: "-20px",
                    right: "-20px",
                  }}
                  onClick={() => setTobeEdited(null)}
                >
                  x
                </span>
              ) : null}
              <div className="form-group">
                <label htmlFor="ChooseService">Choose Servive</label>
                <select
                  className="form-control"
                  id="ChooseService"
                  value={service_id}
                  onChange={(e) => setServiceID(e.target.value)}
                >
                  <option value={1}>1</option>
                  <option value={2}>2</option>
                  <option value={3}>3</option>
                  <option value={4}>4</option>
                  <option value={5}>5</option>
                </select>
              </div>

              <div className="form-group">
                <label htmlFor="categoryname">Category Title</label>
                <input
                  type="text"
                  className="form-control"
                  id="categoryname"
                  value={title}
                  onChange={(e) => setTitle(e.target.value)}
                  placeholder="Category title"
                />
              </div>
              <div className="form-group">
                <label htmlFor="category-description">
                  Category Description
                </label>
                <textarea
                  className="form-control"
                  id="category-description"
                  rows={3}
                  value={description}
                  onChange={(e) => setDescription(e.target.value)}
                />
              </div>
              <button
                type="submit"
                class="btn text-light hard-brand-background"
                onClick={(e) => {
                  e.preventDefault();
                  if (loadingPost) return;
                  postOnServer();
                }}
              >
                {loadingPost ? "Posting..." : tobeEdited ? "Update" : "Create"}
              </button>
            </form>
          </div>
          <div className="col-sm-6">
            <h5 className="hard-brand-color">Category's List</h5>
            <ul className="list-group">
              <li className="list-group-item  text-light hard-brand-background">
                Categories List{" "}
                {loadingCategories ? ". Loading categories" : ""}
              </li>
              {categories.map((cat) => (
                <li
                  type=""
                  className="list-group-item d-flex align-items-center"
                  key={cat.id}
                >
                  {cat.title}
                  <span
                    className="badge  light-brand-color ml-auto"
                    role="button"
                  >
                    <Link to={`/manage-point/${cat.id}`}>Manage</Link>
                  </span>
                  <span
                    className="badge text-warning"
                    role="button"
                    onClick={() => setTobeEdited(cat)}
                  >
                    edit
                  </span>
                  <span
                    className="badge text-danger"
                    role="button"
                    onClick={() => deleteCategory(cat.id)}
                  >
                    delete
                  </span>
                </li>
              ))}
            </ul>
          </div>
        </div>
      </div>
    </div>
  );
};

export default AddServiceCategory;
