import { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import {
  postCategoryPoint,
  getSingleServiceCategory,
} from "../../../api/server";

const CategoryPoint = (props) => {
  const { id } = useParams();
  const [loading, setLoading] = useState(false);
  const [category, setCategory] = useState(null);
  const [categoryError, setCategoryError] = useState(null);
  const [title, setTitle] = useState("");
  const [description, setDescription] = useState("");
  const [postLoading, setPostLoading] = useState(false);
  const [image, setImage] = useState(null);
  const [picture, setImageForUpload] = useState();

  useEffect(() => {
    loadCategory();
  }, []);
  const postCategoryPointForm = async (e) => {
    try {
      setPostLoading(true);
      const formData = new FormData();
      formData.append("title", title);
      formData.append("service_id", category.service_id);
      formData.append("category_id", category.id);

      formData.append("description", description);
      formData.append("picture", picture);
      const response = await postCategoryPoint(formData);
      setPostLoading(false);
    } catch (e) {
      //   console.log(e);
    }
  };
  const uploadImage = async (e) => {
    const file = e.target.files[0];
    try {
      const base64 = await convertBase64(file);
      setImage(base64);
      setImageForUpload(file);
    } catch (e) {
      console.log("Wiered error occured!");
    }
  };
  const convertBase64 = (file) => {
    return new Promise((resolve, reject) => {
      const fileReader = new FileReader();
      fileReader.readAsDataURL(file);
      fileReader.onload = () => {
        resolve(fileReader.result);
      };
      fileReader.onerror = (error) => {
        reject(error);
      };
    });
  };
  const loadCategory = async () => {
    try {
      const categoryList = await getSingleServiceCategory(id);
      if (categoryList && categoryList.data && categoryList.data.success) {
        const receivedList = categoryList.data.contents;
        if (receivedList.length < 1) {
          setCategoryError("This Category doesnt exit...");
        } else {
          setCategory(receivedList[0]);
        }
      }
    } catch (e) {
      //   console.log(e);
    }
  };
  return (
    <div style={{ marginTop: "5rem" }}>
      {categoryError ? (
        <div
          className="container d-flex justify-content-center align-items-center"
          style={{ minHeight: "15rem" }}
        >
          <p className="lead display-4 light-brand-color">{categoryError}</p>
        </div>
      ) : (
        <div className="container">
          <div className="row justify-content-between">
            <div className="col-sm-10 mx-auto text-center">
              <h4 className="light-brand-color">
                <span className="d-inline-block border-bottom py-2">
                  Points for - {category ? category.title : ""}
                </span>
              </h4>
            </div>
            <div className="col-sm-5">
              <h5 className="text-center light-brand-color">
                Create new point
              </h5>
              <form>
                <div className="form-group">
                  <label htmlFor="title">Title</label>
                  <input
                    type="text"
                    className="form-control"
                    id="title"
                    placeholder="Title"
                    value={title}
                    onChange={(e) => setTitle(e.target.value)}
                  />
                </div>
                <div className="form-group">
                  <label htmlFor="description">Description</label>
                  <textarea
                    className="form-control"
                    id="description"
                    value={description}
                    onChange={(e) => setDescription(e.target.value)}
                    rows={3}
                  />
                </div>
                <div className="input-group mb-3">
                  <div className="custom-file">
                    <input
                      type="file"
                      className="custom-file-input"
                      id="inputGroupFile04"
                      onChange={uploadImage}
                      accept="image/*"
                    />
                    <label
                      className="custom-file-label"
                      htmlFor="inputGroupFile04"
                    >
                      Choose picture
                    </label>
                  </div>
                </div>

                <button
                  className="btn light-brand-background text-light"
                  type="submit"
                  onClick={(e) => {
                    e.preventDefault();
                    postCategoryPointForm();
                  }}
                >
                  Save Point
                </button>
              </form>
            </div>
            <div className="col-sm-5">
              <h5 className="light-brand-color">Sample look of the card</h5>
              {description.length || title.length ? (
                <div className="card" style={{ width: "18rem" }}>
                  <img
                    className="card-img-top"
                    src={image}
                    alt="Card image cap"
                  />
                  <div className="card-body">
                    <h5 className="card-title light-brand-color">{title}</h5>
                    <p className="card-text">{description}</p>
                    <a href="#" className="btn btn-secondary" disabled={true}>
                      Disabled
                    </a>
                  </div>
                </div>
              ) : null}
            </div>
          </div>
          {/* Below we're going to post a list of all of your products */}
          <div className="row"></div>
        </div>
      )}
    </div>
  );
};

export default CategoryPoint;
