import { useEffect, useState } from "react";
import { updateSocialMediaApi, getSocialMediaApi } from "../../../api/server";
import Nav from "../nav";
const SocialMediaManage = (props) => {
  const [telegram, setTelegram] = useState();
  const [twitter, setTwitter] = useState();
  const [youtube, setYoutube] = useState();
  const [facebook, setFacebook] = useState();
  const [instagram, setInstagram] = useState();
  const [updateLoading, setUpdateLoading] = useState(false);
  const [loading, setLoading] = useState(false);
  const [aboutus, setAboutUs] = useState(null);
  const [trademark, setTradeMark] = useState(null);
  const [email, setEmail] = useState();
  const [location, setLocation] = useState();
  const [phone, setPhone] = useState();
  const [house, setHouse] = useState();
  const [street, setStreet] = useState();
  const [region, setRegion] = useState();
  const updateSocialMedia = async (social) => {
    setUpdateLoading(true);
    try {
      const response = await updateSocialMediaApi({
        telegram:
          telegram !== null && telegram !== undefined && telegram.trim() !== ""
            ? telegram.trim()
            : null,
        twitter:
          twitter !== null && twitter !== undefined && twitter.trim() !== ""
            ? twitter.trim()
            : null,
        facebook:
          facebook !== null && facebook !== undefined && facebook.trim() !== ""
            ? facebook.trim()
            : null,
        instagram:
          instagram !== null &&
          instagram !== undefined &&
          instagram.trim() !== ""
            ? instagram.trim()
            : null,
        youtube:
          youtube !== null && youtube !== undefined && youtube.trim() !== ""
            ? youtube.trim()
            : null,
        aboutus:
          aboutus !== null && aboutus !== undefined && aboutus.trim() !== ""
            ? aboutus.trim()
            : null,
        trademark:
          trademark !== null &&
          trademark !== undefined &&
          trademark.trim() !== ""
            ? trademark.trim()
            : null,
        email:
          email !== null && email !== undefined && email.trim() !== ""
            ? email.trim()
            : null,
        location:
          location !== null && location !== undefined && location.trim() !== ""
            ? location.trim()
            : null,
        phone:
          phone !== null && phone !== undefined && phone.trim() !== ""
            ? phone.trim()
            : null,
        street:
          street !== null && street !== undefined && street.trim() !== ""
            ? street.trim()
            : null,
        house:
          house !== null && house !== undefined && house.trim() !== ""
            ? house.trim()
            : null,
        region:
          region !== null && region !== undefined && region.trim() !== ""
            ? region.trim()
            : null,
      });
    } catch (e) {
      //   console.log(e)
    }
    setUpdateLoading(false);
  };

  const getSocialMedia = async () => {
    setLoading(true);
    try {
      const response = await getSocialMediaApi();
      // console.log(response);
      if (response && response.data.success && response.data.payload) {
        setInstagram(response.data.payload[0].instagram);
        setFacebook(response.data.payload[0].facebook);
        setYoutube(response.data.payload[0].youtube);
        setTelegram(response.data.payload[0].telegram);
        setTwitter(response.data.payload[0].twitter);
        setAboutUs(response.data.payload[0].aboutus);
        setTradeMark(response.data.payload[0].trademark);
        setLocation(response.data.payload[0].location);
        setEmail(response.data.payload[0].email);
        setPhone(response.data.payload[0].phone);
        setStreet(response.data.payload[0].street);
        setHouse(response.data.payload[0].house);
        setRegion(response.data.payload[0].region);
      } else {
        setInstagram(null);
        setFacebook(null);
        setYoutube(null);
        setTelegram(null);
        setTwitter(null);
        setAboutUs(null);
        setTradeMark(null);
        setLocation(null);
        setEmail(null);
        setPhone(null);
        setStreet(null);
        setHouse(null);
        setRegion(null);
      }
    } catch (e) {
      //  console.log(e)
    }
    setLoading(false);
  };

  useEffect(() => {
    getSocialMedia(); //just getting the social media right
  }, [updateLoading]);
  return (
    <div style={{ marginTop: "5rem" }}>
      <Nav />
      <div className="container">
        <div className="row justify-content-center border-bottom mb-3 pb-2">
          <div className="col-sm-11">
            <h4 className="light-brand-color">Manage Our social Media</h4>
          </div>
        </div>
      </div>
      <div className="container">
        <div className="row">
          <div className="col-sm-5">
            <div>
              <div className="input-group mb-3">
                <div className="input-group-prepend">
                  <span
                    className="input-group-text light-brand-background"
                    id="basic-addon1"
                  >
                    <span className="fa fa-facebook icon-size text-light"></span>
                  </span>
                </div>
                <input
                  type="text"
                  className="form-control"
                  placeholder="Instagram"
                  aria-label="Instagram"
                  aria-describedby="basic-addon1"
                  value={facebook}
                  onChange={(e) => setFacebook(e.target.value)}
                />
              </div>
              <div className="input-group mb-3">
                <div className="input-group-prepend">
                  <span
                    className="input-group-text light-brand-background"
                    id="basic-addon1"
                  >
                    <span className="fa fa-instagram icon-size text-light"></span>
                  </span>
                </div>
                <input
                  type="text"
                  className="form-control"
                  placeholder="Instagram"
                  aria-label="Instagram"
                  aria-describedby="basic-addon1"
                  value={instagram}
                  onChange={(e) => setInstagram(e.target.value)}
                />
              </div>
              <div className="input-group mb-3">
                <div className="input-group-prepend">
                  <span
                    className="input-group-text light-brand-background"
                    id="basic-addon1"
                  >
                    <span className="fa fa-twitter icon-size text-light"></span>
                  </span>
                </div>
                <input
                  type="text"
                  className="form-control"
                  placeholder="Twitter handle"
                  aria-label="Twitter handle"
                  aria-describedby="basic-addon1"
                  value={twitter}
                  onChange={(e) => setTwitter(e.target.value)}
                />
              </div>
              <div className="input-group mb-3">
                <div className="input-group-prepend">
                  <span
                    className="input-group-text light-brand-background"
                    id="basic-addon1"
                  >
                    <span className="fa fa-youtube icon-size text-light"></span>
                  </span>
                </div>
                <input
                  type="text"
                  className="form-control"
                  placeholder="Youtube URL"
                  aria-label="Youtube URL"
                  aria-describedby="basic-addon1"
                  value={youtube}
                  onChange={(e) => setYoutube(e.target.value)}
                />
              </div>
              <div className="input-group mb-3">
                <div className="input-group-prepend">
                  <span
                    className="input-group-text light-brand-background"
                    id="basic-addon1"
                  >
                    <span className="fa fa-telegram icon-size text-light"></span>
                  </span>
                </div>
                <input
                  type="text"
                  className="form-control"
                  placeholder="Telegram"
                  aria-label="Telegram"
                  aria-describedby="basic-addon1"
                  value={telegram}
                  onChange={(e) => setTelegram(e.target.value)}
                />
              </div>
              <div className="input-group mb-3">
                <div className="input-group-prepend">
                  <span
                    className="input-group-text light-brand-background"
                    id="basic-addon1"
                  >
                    <span className="fa fa-envelope icon-size text-light"></span>
                  </span>
                </div>
                <input
                  type="email"
                  className="form-control"
                  placeholder="Email"
                  aria-label="Email"
                  aria-describedby="basic-addon1"
                  value={email}
                  onChange={(e) => setEmail(e.target.value)}
                />
              </div>
              <div className="input-group mb-3">
                <div className="input-group-prepend">
                  <span
                    className="input-group-text light-brand-background"
                    id="basic-addon1"
                  >
                    <span className="fa fa-envelope icon-size text-light"></span>
                  </span>
                </div>
                <input
                  type="text"
                  className="form-control"
                  placeholder="Mobile"
                  aria-label="Mobile"
                  aria-describedby="basic-addon1"
                  value={phone}
                  onChange={(e) => setPhone(e.target.value)}
                />
              </div>
              <button
                type="button"
                class="btn text-light light-brand-background btn-lg btn-block"
                onClick={() => {
                  if (loading) return;
                  updateSocialMedia();
                }}
              >
                {loading ? "Updating..." : "Update Social Media"}
              </button>
            </div>
          </div>
          <div className="col-sm-5">
            <div class="form-group">
              <label for="exampleFormControlTextarea1">About us shortly</label>
              <textarea
                class="form-control"
                id="exampleFormControlTextarea1"
                rows="3"
                value={aboutus}
                onChange={(e) => setAboutUs(e.target.value)}
              ></textarea>
            </div>
            {/* started here */}
            <div>
              <div className="form-group">
                <label htmlFor="exampleFormControlInpur1">
                  Our office building
                </label>
                <input
                  type="text"
                  className="form-control"
                  id="exampleFormControlInpur1"
                  placeholder="PSSSF House 11th & 06th Floor"
                  value={house}
                  onChange={(e) => setHouse(e.target.value)}
                />
              </div>
              <div className="form-group">
                <label htmlFor="exampleFormControlInputx">Street</label>
                <input
                  type="text"
                  className="form-control"
                  id="exampleFormControlInputx"
                  value={street}
                  onChange={(e) => setStreet(e.target.value)}
                  placeholder="Samora Avenue/ Morogoro Road."
                />
              </div>
              <div className="form-group">
                <label htmlFor="exampleFormControlInputdq1">Region</label>
                <input
                  type="text"
                  className="form-control"
                  value={region}
                  onChange={(e) => setRegion(e.target.value)}
                  id="exampleFormControlInputdq1"
                  placeholder="Ilala CBD, Dar es Salaam, Tanzania"
                />
              </div>
            </div>

            {/* ended here */}
            <div class="form-group">
              <label for="exampleFormControlTextarea1">Trademark</label>
              <textarea
                class="form-control"
                id="exampleFormControlTextarea1"
                rows="3"
                placeholder="Jadeja Farming LTD @ 2020"
                value={trademark}
                onChange={(e) => setTradeMark(e.target.value)}
              ></textarea>
            </div>
            <div class="form-group">
              <label for="exampleFormControlTextarea1">
                Location (Google map)
              </label>
              <textarea
                class="form-control"
                id="exampleFormControlTextarea1"
                rows="3"
                placeholder="Google Map embedd"
                value={location}
                onChange={(e) => setLocation(e.target.value)}
              ></textarea>
            </div>
            <div
              class="form-group"
              style={{ maxHeight: "300px", overflowY: "scroll" }}
            >
              <div
                dangerouslySetInnerHTML={{
                  __html: location,
                }}
              ></div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default SocialMediaManage;
