const MeetTheTeam = (props) => {
  return (
    <div className="bg-light pt-3 mt-5">
      <div className="container mb-2">
        <div className="row justify-content-center py-4">
          <h3 className="col-sm-5 simple-title text-center display-4 light-brand-color">
            Meet Our Team
          </h3>
        </div>
      </div>
      {/* end of title */}
      <TeamMember />
    </div>
  );
};

export default MeetTheTeam;

const TeamMember = (props) => {
  return (
    <section className="container">
      <StuffRow
        vector="left"
        position="C.E.0"
        name="Juma Hassani"
        url="../../assets/staffs/man-office1.jpg"
      />
      <StuffRow
        vector="right"
        position="Human Resource"
        name="Judith Jackson"
        url="../../assets/staffs/man-office4.jpg"
      />
      <StuffRow
        vector="left"
        position="Project Manager"
        name="Saidi Husein"
        url="../../assets/staffs/man-office2.jpg"
      />
      <StuffRow
        vector="right"
        position="Team Lead"
        name="Mfaume Issa"
        url="../../assets/staffs/man-office3.jpg"
      />
      <StuffRow
        vector="left"
        position="Accountant"
        name="James Jimmie"
        url="../../assets/staffs/man-office1.jpg"
      />
    </section>
  );
};

const StuffRow = (props) => {
  return (
    <div className="row no-gutters justify-content-center align-items-center pb-5">
      <div className="col-sm-5">
        <SlightedPhoto rot={props.vector} {...props} />
      </div>
      <div className="col-sm-7 light-lined-content pl-4">
        <h4 className="display-5 pale-brand-color m-1 p-0 border-bottom">
          {props.position}
        </h4>
        <h2 className="display-5 pale-brand-color m-1 p-0 border-bottom">
          {props.name}
        </h2>
        <p className="lead">
          Tempor id tempor culpa velit fugiat commodo velit in reprehenderit
          minim non laboris. Ullamco cillum laboris nostrud aliqua cupidatat
          non. Aliquip fugiat ad veniam aute excepteur reprehenderit ea sit
          laboris anim minim minim esse deserunt. Et tempor eiusmod exercitation
          quis laborum anim sit id amet non. Excepteur mollit anim sint
          consequat. Id nisi in deserunt id ut mollit sint.
        </p>
      </div>
    </div>
  );
};
const SlightedPhoto = (props) => {
  const { rot, name, position } = props;
  return (
    <div
      className={`w-75 slighted-photo-container ${
        rot === "left" ? "rotate-left" : "rotate-right"
      }`}
    >
      <img src={props.url} className="img-fluid" />
      <div className="px-2 py-2">
        <h4 className="light-brand-color px-auto">{name}</h4>
        <p className="h3 light-brand-color">{position}</p>
      </div>
    </div>
  );
};
