import Axios from "axios";

export const BASE_URL = "http://127.0.0.1:5050/";
export const BASE_URL_SRC = "http://127.0.0.1:5050";

export const postStaffs = async (payload) => {
  return Axios.post(`${BASE_URL}users`, payload, {
    headers: {
      "Content-Type": "multipart/form-data",
    },
  });
};

/**Getting back all staffs list */
export const getAllStaffs = async () => {
  return Axios.get(`${BASE_URL}users`);
};

export const postService = async (service) => {
  return Axios.post(`${BASE_URL}service`, service);
};
export const deleteStaffApi = async (id) => {
  return Axios.delete(`${BASE_URL}users/delete/${id}`);
};

export const getAllServices = async () => {
  return Axios.get(`${BASE_URL}services`);
};

export const deleteServiceRecord = async (id) => {
  return Axios.delete(`${BASE_URL}service/${id}`);
};

export const getSingleServiceApi = async (id) => {
  return Axios.get(`${BASE_URL}service/${id}`);
};

export const editServiceApi = async (name, id) => {
  return Axios.put(`${BASE_URL}service/${id}`, { name });
};

/*** Lets work on service category
 * ================================
 */

export const createServiceCategory = async (payload) => {
  return Axios.post(`${BASE_URL}category`, payload);
};

export const deleteServiceCategory = async (id) => {
  return Axios.delete(`${BASE_URL}category/${id}`);
};
export const getAllServiceCategories = async (id) => {
  return Axios.get(`${BASE_URL}categories/${id}`);
};

export const getSingleServiceCategory = async (id) => {
  return Axios.get(`${BASE_URL}category/${id}`);
};

export const postCategoryPoint = async (payload) => {
  return Axios.post(`${BASE_URL}cat-point`, payload);
};

export const getSocialMediaApi = async () => {
  return Axios.get(`${BASE_URL}social/retrieve`);
};
export const updateSocialMediaApi = async (social) => {
  return Axios.post(`${BASE_URL}social/update`, social);
};

// slideshow api
export const getAllSlidesApi = () => {
  return Axios.get(`${BASE_URL}allslides`);
};

export const singleSlideApi = (id) => {
  return Axios.get(`${BASE_URL}slide/${id}`);
};

export const createSlideApi = (payload) => {
  return Axios.post(`${BASE_URL}slide`, payload);
};

export const updateSlideApi = (payload) => {
  return Axios.put(`${BASE_URL}slide/${payload.id}`, payload);
};
export const deleteSlideApi = (id) => {
  return Axios.delete(`${BASE_URL}slide/${id}`);
};
