import { useEffect, useState } from "react";
import {
  postStaffs,
  getAllStaffs,
  BASE_URL,
  BASE_URL_SRC,
  deleteStaffApi,
} from "../../../api/server";
import { colors } from "../../utils/colors";
import Nav from "../nav";

const PostStaff = (props) => {
  const [name, setName] = useState("");
  const [position, setPosition] = useState("");
  const [description, setDescription] = useState("");
  const [loading, setLoading] = useState(false);
  const [staffs, setStaffs] = useState([]);
  const [postLoading, setPostLoading] = useState(false);
  const [image, setImage] = useState(null);
  const [picture, setImageForUpload] = useState();
  const [deleteStaffLoading, setDeleteStaffLoading] = useState({
    state: false,
    id: null,
  });

  const deleteStaff = async (id) => {
    console.log("Called deleting");
    setDeleteStaffLoading({ state: true, id: id });
    try {
      // in here w're deleting a user from
      //and we update a state after that
      await deleteStaffApi(id);
    } catch (e) {}
    setDeleteStaffLoading({ state: false, id: id });
  };
  const postNewStaff = async (e) => {
    e.preventDefault();
    try {
      setPostLoading(true);
      const formData = new FormData();
      formData.append("name", name);
      formData.append("position", position);
      formData.append("description", description);
      formData.append("picture", picture);
      const response = await postStaffs(formData);
      console.log(response);
      setStaffs([...staffs, response.data.payload]);
      setPostLoading(false);
    } catch (e) {
      //   console.log(e);
    }
  };

  useEffect(() => {
    getStaffList();
  }, [deleteStaffLoading.id]);

  const getStaffList = async () => {
    try {
      setLoading(true);
      const response = await getAllStaffs();
      setStaffs(response.data.contents || []);
    } catch (e) {}
    setLoading(false);
  };
  /**converting images to picking and base64 */
  const uploadImage = async (e) => {
    const file = e.target.files[0];
    try {
      const base64 = await convertBase64(file);
      setImage(base64);
      setImageForUpload(file);
    } catch (e) {
      console.log("Wiered error occured!");
    }
  };
  const convertBase64 = (file) => {
    return new Promise((resolve, reject) => {
      const fileReader = new FileReader();
      fileReader.readAsDataURL(file);
      fileReader.onload = () => {
        resolve(fileReader.result);
      };
      fileReader.onerror = (error) => {
        reject(error);
      };
    });
  };
  //   console.log(staffs);
  return (
    <div style={{ marginTop: "5rem" }}>
      <Nav />
      <div className="container">
        {!loading ? (
          <div>
            <div className="row justify-content-center">
              <div className="col-sm-8">
                <h4 className="text-center light-brand-color">Add Staff</h4>
                <div className="row justify-content-center">
                  <div className="col-sm-6 text-center">
                    <img
                      src={image}
                      className="img-fluid rounded-circle"
                      style={{
                        borderRadius: "50%",
                        objectFit: "contain",
                        width: "200px",
                        height: "200px",
                      }}
                    />
                  </div>
                </div>
                <form>
                  <div className="form-group">
                    <label htmlFor="name">Staff Name</label>
                    <input
                      type="text"
                      className="form-control"
                      id="name"
                      value={name}
                      onChange={(e) => setName(e.target.value)}
                      placeholder="Bakari Juma"
                    />
                  </div>
                  <div className="form-group">
                    <label htmlFor="position">Staff Role</label>
                    <input
                      type="text"
                      className="form-control"
                      id="position"
                      value={position}
                      onChange={(e) => setPosition(e.target.value)}
                      placeholder="General Manager"
                    />
                  </div>
                  <div className="form-group">
                    <label htmlFor="position">Select Picture</label>
                    <div className="custom-file">
                      <input
                        type="file"
                        className="custom-file-input"
                        id="validatedCustomFile"
                        required
                        onChange={uploadImage}
                        accept="image/*"
                      />
                      <label
                        className="custom-file-label"
                        htmlFor="validatedCustomFile"
                      >
                        Choose file...
                      </label>
                      <div className="invalid-feedback">
                        Example invalid custom file feedback
                      </div>
                    </div>
                  </div>

                  <div className="form-group">
                    <label htmlFor="description">Description</label>
                    <textarea
                      className="form-control"
                      id="description"
                      value={description}
                      onChange={(e) => setDescription(e.target.value)}
                      rows={3}
                    />
                  </div>
                  <button
                    type="submit"
                    class="btn text-light hard-brand-background"
                    onClick={(e) => {
                      e.preventDefault();
                      if (postLoading) return;
                      postNewStaff(e);
                    }}
                  >
                    {postLoading ? "Posting..." : "Add Staff"}
                  </button>
                </form>
              </div>
              {/* <div className="col-sm-3">
              </div> */}
            </div>
            <div className="row mt-5">
              <div className="col-sm-11">
                <h5>Team members</h5>
                {staffs
                  ? staffs.length > 0
                    ? staffs.map((st, i) => (
                        <div className="media my-3" key={i}>
                          <img
                            className="mr-3"
                            src={`${BASE_URL_SRC}${st.picture}`}
                            style={{
                              width: "160px",
                              height: "160px",
                              borderRadius: "50%",
                              objectFit: "cover",
                              borderColor: colors.brandLight,
                              borderStyle: "solid",
                              padding: "2px",
                              borderWidth: "2px",
                            }}
                            alt="Generic placeholder image"
                          />
                          <div className="media-body mt-2">
                            <p className="mt-0 mb-0 light-brand-color font-weight-bold">
                              {st.name}
                            </p>
                            <p className="mt-0 mb-0 light-brand-color font-weight-bold">
                              {st.position}
                            </p>
                            <p className="lead"> {st.description}</p>
                            <div
                              className="d-flex justify-content-space align-items-center"
                              style={{ width: "20%" }}
                            >
                              <span
                                className="fa fa-edit text-warning mr-4 icon-size"
                                role="button"
                              ></span>
                              {deleteStaffLoading.state &&
                              deleteStaffLoading.id === st.id ? (
                                <span className="text-danger">deleting...</span>
                              ) : (
                                <span
                                  className="fa fa-trash text-danger  icon-size"
                                  role="button"
                                  onClick={() => deleteStaff(st.id)}
                                ></span>
                              )}
                            </div>
                          </div>
                        </div>
                      ))
                    : null
                  : null}
              </div>
            </div>
          </div>
        ) : null}{" "}
        {loading ? (
          <div className="row justify-content-center">
            <div className="col-sm-8">
              <h3 className="light-brand-color">Please wait...</h3>
            </div>
          </div>
        ) : null}
      </div>
    </div>
  );
};

export default PostStaff;
