import { useState, useEffect, useRef } from "react";
import {
  getAllServices,
  postService,
  deleteServiceRecord,
  getSingleServiceApi,
  editServiceApi,
} from "../../../api/server";

const { default: Nav } = require("../nav");

const ManageServices = (props) => {
  const [name, setName] = useState();
  const [waitNamePost, setWaitNamePost] = useState(false);
  const [services, setSevices] = useState([]);
  const [deleteWait, setDeleteWait] = useState(false);
  const [editing, setEditing] = useState(null);
  const inputRef = useRef();
  const postNewService = async () => {
    setWaitNamePost(true);
    try {
      if (editing) {
        const response = await editServiceApi(name, editing.id);
        setEditing(null);
      } else {
        const response = await postService({ name });
      }
    } catch (e) {
      //   console.log(e)
    }
    getServices();
    setWaitNamePost(false);
  };

  const getServices = async () => {
    try {
      const response = await getAllServices();
      setSevices(response.data.contents);
    } catch (e) {
      console.log(e);
    }
  };

  const getSingleService = async (id) => {
    try {
      const response = await getSingleServiceApi(id);
      if (response.data.contents) {
        setEditing(response.data.contents);
        inputRef.current.focus();
        setName(response.data.contents.name);
      }
    } catch (e) {
      console.log(e);
    }
  };

  const deleteService = async (id) => {
    setDeleteWait(true);
    try {
      await deleteServiceRecord(id);
    } catch (e) {
      setDeleteWait(false);
    }

    getServices();
    setDeleteWait(false);
  };
  useEffect(() => {
    getServices();
  }, []);
  return (
    <div className="" style={{ marginTop: "5rem" }}>
      <Nav />
      <div className="container mx-2">
        <div className="row">
          <div className="col-sm-12 mx-auto">
            <h4 className="light-brand-color display-4  light-lined-content-1">
              Manage Services
            </h4>
          </div>
        </div>
      </div>
      <div className="container">
        <div className="row">
          <div className="col-sm-5">
            <h5>Create New Service</h5>
            <form className="form-inline">
              <div className="form-group mx-sm-3 mb-2 position-relative">
                {editing ? (
                  <span
                    onClick={() => setEditing(null)}
                    className="position-absolute text-light bg-warning py-1 px-2"
                    role="button"
                    style={{
                      top: "-10px",
                      right: "-5px",
                      height: "30px",
                      width: "30px",
                      borderRadius: "50%",
                    }}
                  >
                    X
                  </span>
                ) : null}
                <label htmlFor="inputPassword2" className="sr-only">
                  Title
                </label>
                <input
                  type="text"
                  className="form-control"
                  id="inputPassword2"
                  placeholder="Service name"
                  value={name}
                  ref={inputRef}
                  onChange={(e) => setName(e.target.value)}
                />
              </div>
              <button
                type="submit"
                className="btn btn-success mb-2"
                onClick={(e) => {
                  e.preventDefault();
                  if (waitNamePost) return;
                  postNewService();
                }}
              >
                {waitNamePost ? "Posting..." : "Save"}
              </button>
            </form>
            {/* table of services */}
            <table className="table position-relative">
              <thead>
                <tr>
                  <th scope="col">#</th>
                  <th scope="col">Service</th>
                  <th scope="col">Edit</th>
                  <th scope="col">
                    Delete{" "}
                    {deleteWait ? (
                      <span
                        className="position-absolute text-light bg-danger px-2 py-1"
                        style={{
                          top: "-10px",
                          right: "-5px",
                          borderRadius: "3px",
                        }}
                      >
                        Deleting...
                      </span>
                    ) : null}
                  </th>
                </tr>
              </thead>
              <tbody>
                {services.map((serv, i) => (
                  <tr key={i}>
                    <th scope="row">{i + 1}</th>
                    <td>{serv.name}</td>
                    <td>
                      <span
                        className="text-warning cursor-pointer"
                        role="button"
                        onClick={() => {
                          getSingleService(serv.id);
                        }}
                      >
                        edit
                      </span>
                    </td>

                    <td>
                      <span
                        style={{ curser: "pointer" }}
                        role="button"
                        className="text-danger font-weight-bold"
                        onClick={() => {
                          deleteService(serv.id);
                        }}
                      >
                        X
                      </span>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ManageServices;
