import Nav from "../nav";
import { useState, useEffect } from "react";
import {
  createSlideApi,
  getAllServices,
  getAllSlidesApi,
  deleteSlideApi,
} from "../../../api/server";
const SlideShowForm = (props) => {
  const [loading, setLoading] = useState(false);
  const [linkTo, setLinkTo] = useState();
  const [brief, setBrief] = useState();
  const [slides, setSlides] = useState([]);
  const [services, setServices] = useState([]);
  const [postLoading, setPostLoading] = useState(false);
  const [loadingPost, setLoadingPost] = useState(false);
  const [deleteWait, setDeleteWait] = useState({ id: null, wait: false });
  const [maxCount, setMaxCount] = useState(false);
  const postSlide = async () => {
    setPostLoading(true);
    try {
      const resp = await createSlideApi({ brief, service_id: linkTo });
    } catch (e) {
      // console.log(e);
    }
    setPostLoading(false);
  };
  const deleteBrief = async (id) => {
    setDeleteWait({ id, wait: true });
    try {
      const resp = await deleteSlideApi(id);
    } catch (e) {}
    setDeleteWait({ id, wait: false });
  };
  const getAllservices = async () => {
    try {
      const servs = await getAllServices();
      if (servs && servs.data && servs.data.success) {
        setServices(servs.data.contents);
      }
    } catch (e) {
      setServices([]);
    }
  };
  const getAllSlides = async () => {
    try {
      const allSlides = await getAllSlidesApi();
      if (allSlides && allSlides.data && allSlides.data.success) {
        setSlides(allSlides.data.contents);
        if (slides.length > 5) {
          setMaxCount(true);
        }
      }
    } catch (e) {
      // console.log(e)
      setServices([]);
    }
  };

  useEffect(() => {
    if (!postLoading) {
      getAllSlides();
    }
  }, [postLoading, deleteWait.wait]);
  useEffect(() => {
    getAllSlides();
    getAllservices();
  }, []);
  return (
    <div style={{ marginTop: "5rem" }}>
      <Nav />
      <div className="container">
        <div className="row justify-content-center border-bottom mb-3 pb-2">
          <div className="col-sm-11">
            <h4 className="light-brand-color">
              Manage Landing Page slide shows
            </h4>
          </div>
        </div>
      </div>
      {/* end of the title */}
      <div className="container">
        <div className="row">
          <div className="col-sm-6">
            <form>
              <div className="form-group">
                <label htmlFor="exampleFormControlSelect1">
                  Service To Link
                </label>
                <select
                  className="form-control"
                  id="exampleFormControlSelect1"
                  onChange={(e) => setLinkTo(e.target.value)}
                >
                  {services.map((srv) => (
                    <option value={srv.id} key={srv.id}>
                      {srv.name}
                    </option>
                  ))}
                </select>
              </div>
              <div className="form-group">
                <label htmlFor="readd1">Brief Info</label>
                <textarea
                  className="form-control"
                  id="readd1"
                  rows={3}
                  value={brief}
                  onChange={(e) => setBrief(e.target.value)}
                />
              </div>
              <button
                type="button"
                onClick={() => {
                  if (postLoading || maxCount) return;
                  postSlide();
                }}
                className="btn light-brand-background text-light"
              >
                Add Slideshow
              </button>
              {maxCount ? (
                <p className="lead">Cant add more than 6 slide show text</p>
              ) : null}
            </form>
          </div>
          <div className="col-sm-6">
            {slides.map((sld, idx) => (
              <div>
                <p className="lead text-right py-3" key={idx}>
                  {sld.brief}
                  {deleteWait.id === sld.id && deleteWait.wait ? (
                    <span className="text-danger d-block">Deleting...</span>
                  ) : (
                    <span
                      className="fa fa-trash icon-size text-danger text-right d-block"
                      role="button"
                      onClick={() => deleteBrief(sld.id)}
                    ></span>
                  )}
                </p>
              </div>
            ))}
          </div>
        </div>
      </div>
    </div>
  );
};

export default SlideShowForm;
